html, body {
  height: 95%;
  margin: 0;
  padding: 0;
}
body {
  font-family: 'Montserrat', sans-serif;

}


.bgg { 
  background-image: url('images/background.jpg') ; 
  background-position: center;
  background-repeat: no-repeat;
  background-size:100% 100%;
  height: 100%;
  position: fixed;
  left: 0;
  right: 0;
  z-index: -1;
  width: 100%;
  height: 100vh;
  margin: 0;
  padding: 0;
  margin-top: 0;
  /* filter: blur(5px);
  -webkit-filter: blur(5px); */
}

.menu {
  background-image: url('images/menu.jpg') ; 
  background-position: center;
  background-repeat: no-repeat;
  background-size:74% 84%;
  height: 100%;
  position: fixed;
  left: 0;
  right: 0;
  z-index: 0;
  width: 100%;
  height: 100vh;
  margin: 0;
  padding: 0;
}

.blurred{
  background-image: url('images/background.jpg') ; 
  background-position: center;
  background-repeat: no-repeat;
  background-size:100% 100%;
  height: 100%;
  position: fixed;
  left: 0;
  right: 0;
  z-index: -1;
  width: 100%;
  height: 100vh;
  margin: 0;
  padding: 0;
  margin-top: 0px;
  filter: blur(5px);
  -webkit-filter: blur(5px); 
}

h1.header{
  display: inline;
  margin-left: 20px;
  color: rgb(255, 255, 255);
  font-size: 300%;
  z-index: 2;
}

p.intro{
  color: rgb(255, 255, 255);
  font-size: 350%;
  vertical-align: middle;
  text-align: center;
  margin-top: 7%;
  position: fixed;
  left: 0;
  right: 0;
  z-index: 2;
}

p.clearIntro{
  display: none;
}

.color{
  height: 150px;
  position: fixed;
  z-index: 1;
  left: 0;
  right: 0;
  background-color: black;
  opacity: 0.6;
  margin-top: 4%;
}
.colorless{
  height: 150px;
  position: fixed;
  z-index: 1;
  left: 0;
  right: 0;
  background-color: black;
  opacity: 0;
  margin-top: 4%;
}

.tab-bar{
  height: 40px;
  position: fixed;
  z-index: 1;
  left: 0;
  right: 0;
  margin-left: 72%;
  
}

.tab-color{
  height: 40px;
  position: fixed;
  z-index: 1;
  left: 0;
  right: 0;
  background-color: black;
  opacity: 0.4;
  margin-left: 74%;
  
}

.btnNav{
  float: right;
  font-family: 'Birdseye', sans-serif;
  font-size: 16px;
  font-weight: 500;
  -webkit-font-smoothing: antialiased;
  color: #ffffff;
  display: block;
  padding: 12px 20px;
  border: 1px solid transparent;
  font-family: 'Montserrat', sans-serif;
  font-size: 20px;
  font-weight: 500;
  line-height: 1;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  text-decoration: none;
  background-color: Transparent;
  background-repeat:no-repeat;
  border: none;
  cursor:pointer;
  overflow: hidden;
  outline:none;
  margin-left: 6px;
  /* -webkit-transition: opacity 300ms ease;
  -moz-transition: opacity 300ms ease;
  -ms-transition: opacity 300ms ease;
  transition: opacity 300ms ease; */
}

